.App {
  text-align: center;
}

.slider {
  height:500px; 
  background-image: url('../public/img/slider.jpg');
  background-size: cover;
  background-position: center;
}

.c1 {text-decoration: none; color: #333; text-align: center;}
.c1:hover {color: red;}

.start {
  transform: scale(0);
}

.end {
  transform: scale(1);
  transition: all 0.5s;
}

.fade_start {
  opacity: 0;
}

.fade_end {
  opacity: 1;
  transition: all 0.5s;
}